* {
    box-sizing: border-box;
}

body {
    margin: 0;
}

.gray {
    color: #918E9B;
}

/* NAVBAR CSS */
nav {
    display: flex;
    height: 70px;
    box-shadow: 0 4px 4px 0 rgba(0,0,0,.2);
    padding: 20px;
}

.nav--logo {
    max-width: 100px;
}

/* END NAVBAR CSS */

/* HEADER CSS */

header {
    display: flex;
    flex-direction: column;
    padding: 20px;
}



.hero--h1 {
    font-size: 2.1rem;
    padding-top: 10px;
    margin-bottom: 5px;
}

.hero--p {
    font-size: 1.1rem;
    line-height: 1.4rem;
    padding: 5px;
  
} 

/* END HEADER CSS */

/* CARD CSS */

.card--one {
    width:195px;
    font-size: 14px;
    flex: 0 0 auto;         
    display: flex;          
    flex-direction: column; 
    position: relative;

   
}

.card--info {
    display: flex;
    align-items: center;
}

.img--katie {
    width: 100%;
    border-radius: 9px;
}

.img--star {
    width: 20px;
    height: 14px;
}

.title {
    display:flex;

    
}


.span--p {
    font-weight: bold;
}

.card--badge {
    position: absolute;
    top: 6px;
    left: 6px;
    background-color: white;
    padding: 5px 7px;
    border-radius: 2px;
    font-weight: bold;
}

.card--list {
    display: flex;
    flex-direction: column;
    align-items: center;
}

@media screen and (min-width: 700px) {
    .card--list {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding: 20px;
    }
  }

  @media screen and (min-width: 900px) {
    .card--list {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding: 70px;
    }
  }



  @media screen and (min-width: 900px) {
    .card--list {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding: 70px;
    }
  }



  @media screen and (min-width: 1920px) {
    .card--list {
        display: flex;
        flex-direction: row;
        width: 60%;
        margin: 0 auto;
        
    }

    .card--one {
       
        width: 250px;
      
    }
    
  }




  @media screen and (min-width: 2500px) {
    .card--list {
        display: flex;
        flex-direction: row;
        width: 60%;
        margin: 0 auto;
        
    }

    .card--one {
       
        width: 350px;
      
    }
    
  }